.App {
  background-color: #404040;
  height: 100vh;
  min-height: 100%;
	display: flex;
  flex-direction: column;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.App-header {
  background-color: #303030;
  min-height: 50px;
  height: auto;
  display: flex;
  align-content: center;
  padding: 10px;
}

.App-body {
  background-color: #404040;
  min-height: 100%;
  height: fit-content;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
