.cardBox {
  perspective: 330px;
  margin: 10px;
}

.card {
  position: relative;
  width: 330px;
  height: 250px;
  background-color: rgba(255, 255, 255, 0.6);
  transform-style: preserve-3d;
  transition: .5s ease;
  box-shadow: 0 30px 30px rgba(0, 0, 0, .5);
  backface-visibility: hidden;
  border-radius: 10px;
}

.card-front, .card-back {
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  border-radius: 10px;
}

.card-back {
  width: 330px;
  height: 250px;
  backface-visibility: hidden;
}

.card-front {
  width: 330px;
  height: 250px;
  font-size: 40px;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: black;
  padding: 10px;
}

.card-back {
  font-size: 30px;
  justify-content: center;
  align-items: center;
  backface-visibility: hidden;
  box-shadow: 0 30px 30px rgba(0, 0, 0, .5);
  text-align: center;
}

.cardBox:hover .card {
  transform: rotateY(180deg);
}

.card-back {
  transform: rotateY(180deg);
}

.card-front-div {
  width: 310px;
  height: 190px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}

.card-image-front {
