}

.card-image-back {
  max-width: 40%;
  max-height: 40%;
  padding-top: 10%
}

.card-title-back {
  font-size: 20px;
  height: 20px;
  margin: 0px;
  padding: 0px;
  padding-top: 50px;
  color: #ECF0F1;
}

.cardEnlarged {
  position: relative;
  width: 100%;
  height: fit-content;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.4);
  transform-style: preserve-3d;
  transition: .5s ease;
  box-shadow: 0 30px 30px rgba(0, 0, 0, .5);
  backface-visibility: hidden;
  border-radius: 10px;
}

.cardEnlargedTop {
  width: 100%;
  height: 30px;
